/* ------------------------------------ ELEMENTS */

div.qholder {
	width: 100%;
    margin-top: 100px;
    margin-bottom: 25px;
    color: #ffffff;
}

/* ------------------------------------ LIST */

li.nobullet {
	list-style-type: none;
}

/* ------------------------------------ BUTTONS */

button.white-delete {
	margin: 0px 10px -5px 10px;
	padding: 2px 10px !important;
	border: 2px solid #ffffff;
	border-radius: 100px;
	background: #ffffff;
	color: #78c9b1;
	font-weight: bold !important;
}

button.white-delete:hover {
	background: -webkit-linear-gradient(135deg, #7ac36e, #78c9b1);
    background: -o-linear-gradient(135deg, #7ac36e, #78c9b1);
    background: -webkit-linear-gradient(315deg, #7ac36e, #78c9b1);
    background: -o-linear-gradient(315deg, #7ac36e, #78c9b1);
    background: linear-gradient(135deg, #7ac36e, #78c9b1);
    color: #ffffff;
}

/* ------------------------------------ HEADERS AND TEXT */

h2.spaced {
	margin-bottom: 15px;
	margin-left: 10px;
}


